import './Login.css';
import React, { useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';

const Login = () => {

  const inputRef = useRef();

  useEffect(() => {
    inputRef.current.focus(); //     id input   ()
  }, [])

  return (
    <section>
      <div className='login_section1'>
      <img src={process.env.PUBLIC_URL + '/img/loginAndCreateAccount.jpg'} alt="LoginImage" />
        <div className='login_section1_in'>
          <div className='login_section1_in_title'>
            <strong> </strong>
            <form>
              <label htmlFor='login_id'></label>
              <input type="email" id='login_id' placeholder=' ' ref={inputRef} />
              <label htmlFor='login_password'></label>
              <input type="password" id='login_password' placeholder='' autoComplete='on' />
              <button type='submit'>
                <span></span>
              </button>
            </form>
            <div className='login_section1_menu'>
              <p> </p>
              <Link to='/createAccount'>
                <p> &gt;</p>
              </Link>
            </div>
            <p> </p>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Login;