import './Container.css';
import React, { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Pagination, EffectCreative, Autoplay } from 'swiper';
import 'swiper/css';
import 'swiper/css/pagination';
import "swiper/css/effect-creative";
import 'swiper/css/autoplay';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';

SwiperCore.use([ Pagination, Autoplay ])

const Container = () => {

  const isMobile = useMediaQuery({ maxWidth: 767 });

  let [id, setId] = useState([]);
  let [mobileFile, setMobileFile] = useState([]);
  let [pcFile, setPcFile] = useState([]);
  let [title, setTitle] = useState([]);
  let [name, setName] = useState([]);
  let [slide, setSlide] = useState([]);

  useEffect(() => {
    axios
    .get(process.env.PUBLIC_URL + "/data/data.json") //   json  (GET )
    .then((res) => { // (res)  

      const { dataSection1 } = res.data; //  (res) 'data'   '{ dataSection1 }'   (  )
      const ids = dataSection1.map((item) => item.id); 
      const mobileFiles = dataSection1.map((item) => item.mobileFile); 
      const pcFiles = dataSection1.map((item) => item.pcFile); 
      const titles = dataSection1.map((item) => item.title); // 'data'    json       
      const names = dataSection1.map((item) => item.name);
      const slides = Array.from({ length: dataSection1.length }, (_, idx) => idx); // slides   (json  'dataSection1' ) 
                                                                           // (idx)     (_)     
      setId(ids);                                                                           
      setMobileFile(mobileFiles);
      setPcFile(pcFiles);
      setTitle(titles); // 'setState'     
      setName(names);   //         
      setSlide(slides);

    })
    .catch(error => {
      console.log(error);
    });
  }, [])

  return (
    <section>
      <div className='section1'>
        <Swiper
          modules={[Pagination, EffectCreative]} effect={"creative"} spaceBetween={0} slidesPerView={1} pagination={{clickable: true}} 
          allowTouchMove={true} loop={true} grabCursor={true} autoplay={{delay: 8000, disableOnInteraction: false}}
          creativeEffect={{prev: {shadow: true, translate: ["-20%", 0, -1],}, next: {translate: ["100%", 0, 0],}}}>

          {slide.map((_, idx) => {
            return (
              <SwiperSlide key={idx}>
                {mobileFile[idx].endsWith('.jpg') ? (
                  <>
                    <img src={isMobile ? process.env.PUBLIC_URL + mobileFile[idx] : process.env.PUBLIC_URL + pcFile[idx]} alt="section1_img" />
                    <div className='section1_title'>
                      <p>{id[idx]}</p>
                      <h2>{name[idx]}</h2>
                      <p>{title[idx]}</p>
                    </div>
                    <div className='section1_more'>
                      <Link to={`/more/${name[idx]}`}><span className='more'>  &gt;</span></Link>
                      {/*      /more/(json  name)  url  */}
                      <span className='send'>  &gt;</span>
                    </div>
                  </>
                ) : (
                  <>
                    <video muted autoPlay loop playsInline src={isMobile ? process.env.PUBLIC_URL + mobileFile[idx] : process.env.PUBLIC_URL + pcFile[idx]} alt="section1_video" />
                    <div className='section1_title'>
                      <p>{id[idx]}</p>
                      <h2>{name[idx]}</h2>
                      <p>{title[idx]}</p>
                    </div>
                  </>
                )}
              </SwiperSlide>
            );
          })}
        </Swiper>
      </div>
    </section>
  );
};

export default Container;