/* eslint-disable no-useless-escape */
import './CreateAccount.css';
import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';

const CreateAccount = () => {
  
  const { register, handleSubmit, watch, formState : {errors} } = useForm({ mode : "onChange" }); 
                                                                  // {mode : "onChange"}    
  const navigate = useNavigate();

  const onSubmit = (data) => {
    console.log('data', data);
    navigate('/CreateAccountComplete'); //     onSubmit  <CreateAccountComplete />  
  }

  // useEffect(() => {
  //   const hasErrors = Object.keys(errors).length > 0 ||
  //   watch('email') === "" || 
  //   watch('password') === "" || 
  //   watch('password_confirm') === "";

  //   // setHasErrors(hasErrors);
  
  // }, [errors, watch])
  
  return (
    <section>
      <div className='create_account_section1'>
      <img src={process.env.PUBLIC_URL + '/img/loginAndCreateAccount.jpg'} alt="CreateAccountImage" />
        <div className='create_account_section1_in'>
          <div className='create_account_section1_in_title'>
            <strong></strong>
            <form onSubmit={handleSubmit(onSubmit)}>
              <label htmlFor='email'> </label>
              <input type="email" id='email' name='email' placeholder=' ' autoFocus
                {...register('email', { required : true, pattern : /^\S+@\S+$/i })} />
                {errors.email && <p className='error_message'>   .</p>}
              <label htmlFor='password'></label>
              <input type="password" id='password' name='password' placeholder='' autoComplete='on'
                {...register('password', { required : true, pattern : /(?=.*\d{1,50})(?=.*[~`!@#$%\^&*()-+=]{1,50})(?=.*[a-zA-Z]{2,50}).{8,16}$/, minLength : 8, maxLength : 16 })} />
                {errors.password && <p className='error_message'> , ,   8~16 .</p>} 
              <label htmlFor='password_confirm'> </label>
              <input type="password" id='password_confirm' name='password_confirm' placeholder=' ' autoComplete='on'
                {...register('password_confirm', { required : true, validate : (value) => value === watch('password')})} />
                {errors.password_confirm && <p className='error_message'>  .</p>}
              <button type='submit'>
                <span></span>
              </button>  
            </form>
            <div className='create_account_section1_menu'>
              <p> </p>
              <Link to='/login'>
                <p> &gt;</p>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default CreateAccount;